import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { useMappedImages } from "../../hooks/images";
import Img from "gatsby-image";

export default function Icons({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "projects/team-portals/icons/2-factors.png"
              "projects/team-portals/icons/data-classification.png"
              "projects/team-portals/icons/bau.png"
              "projects/team-portals/icons/data-protection.png"
              "projects/team-portals/icons/egress.png"
              "projects/team-portals/icons/faqs.png"
              "projects/team-portals/icons/intro.png"
              "projects/team-portals/icons/isc-intro.png"
              "projects/team-portals/icons/isc-team.png"
              "projects/team-portals/icons/iso.png"
              "projects/team-portals/icons/password.png"
              "projects/team-portals/icons/pci.png"
              "projects/team-portals/icons/physical.png"
              "projects/team-portals/icons/policies.png"
              "projects/team-portals/icons/removable-media.png"
              "projects/team-portals/icons/report.png"
              "projects/team-portals/icons/requirements.png"
              "projects/team-portals/icons/spam.png"
              "projects/team-portals/icons/trojan.png"
              "projects/team-portals/icons/iso.png"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <>
      <div className="overflow-hidden">
        <div className="flex flex-wrap -mx-2 mb-8">
          {[
            "projects/team-portals/icons/2-factors.png",
            "projects/team-portals/icons/data-classification.png",
            "projects/team-portals/icons/bau.png",
            "projects/team-portals/icons/data-protection.png",
            "projects/team-portals/icons/egress.png",
            "projects/team-portals/icons/faqs.png",
            "projects/team-portals/icons/intro.png",
            "projects/team-portals/icons/isc-intro.png",
            "projects/team-portals/icons/isc-team.png",
            "projects/team-portals/icons/iso.png",
            "projects/team-portals/icons/password.png",
            "projects/team-portals/icons/pci.png",
            "projects/team-portals/icons/physical.png",
            "projects/team-portals/icons/policies.png",
            "projects/team-portals/icons/removable-media.png",
            "projects/team-portals/icons/report.png",
            "projects/team-portals/icons/requirements.png",
            "projects/team-portals/icons/spam.png",
            "projects/team-portals/icons/trojan.png",
            "projects/team-portals/icons/iso.png",
          ].map((value) => {
            return (
              <div
                className="w-1/4 sm:w-1/5 sm:w-1/6 md:w-1/10 lg:w-1/12 px-2 py-2 "
                onClick={() => {
                  onGalleryImageClick(value);
                }}
              >
                <Img
                  className={"cursor-zoom-in"}
                  fluid={optimizedImages[value].childImageSharp.fluid}
                />
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
}
